*/

&__live {

  // Search live styles
  // -------------------------

  overflow: hidden;

  border-bottom: 1px solid $color-nav-footer-bg;

  width: 100%;

  .live-suggestions {
    display: none;
    height: 0;

    background: $color-search-live-bg;
    color: $color-search-live-text;

    &.is-open {
      display: block;
      height: auto;
    }

    strong {
      @include font-semi-light;
      color: $color-search-live-text-match;
    }

    li {
      padding: 15px 0 16px;

      &:not(:last-child) {
        border-bottom: 1px solid $color-aluminum-80;
      }
    }
  }

  .live-footer {
    display: none;

    height: 0;
    padding: 21px 0 24px;

    background: $color-nav-footer-bg;

    &.is-open {
      display: block;
      height: auto;
    }

    a,
    button {
      &.live-icon,
      &.live-title,
      &.live-link {
        color: $color-nav-link;

        &:focus,
        &:hover {
          color: $color-nav-link--active;
        }
      }
    }

    .offer {
      margin-top: 10px;

      .offer__image {
        float: left;
        margin-right: 18px;

        img {
          max-width: 64px;
          max-height: 64px;
        }
      }

      .offer__content {
        @include typo-body-2;

        display: table;
        word-wrap: normal;
      }
    }
  }
}

