// --------------------------------------------------


// Display-Table wrapper styles
// -------------------------

.table-display {
  @include clearfix;

  display: block;

  .table__wrapper {
    margin-bottom: 16px;

    border-top: 1px solid $color-table-border;
    border-bottom: 1px solid $color-table-border;

    > .row {
      margin: 0;
      padding: 10px 0;

      &:not(:last-child) {
        border-bottom: 1px solid $color-table-border;
      }

      > [class^='col-'] {
        padding: 6px 16px 0;
      }
    }

    // set margin of form fields
    // -------------------------

    .checkbox + .checkbox {
      margin-top: 8px;
    }

    .radio + .radio {
      margin-top: 8px;
    }
  }

  .button {
    margin: 0 16px;
  }

  // Table styles
  // -------------------------

  &--shadow {
    padding: 19px 0 16px;

    box-shadow: 0 0 4px 0 rgba($color-black, .25);
  }
}
