/*doc
---
title: Clearfix
name: 05-utilities-clearfix-01
category: Utilities - Clearfix
---

Easily clear floats by adding `.clearfix` to the parent element. Can also be used as a mixin.

```html
<div class="clearfix">...</div>
```

```scss
// Usage as a mixin
.element {
  @include clearfix;
}
```
*/

.clearfix {
  @include clearfix;
}
