//
// Core
// --------------------------------------------------

* {
  &,
  &::before,
  &::after {
    box-sizing: inherit;
  }
}

/*
::selection {
  background: $color-selection-bg;
  color: $color-selection-fg;
}
*/

html,
body {
  @include body;
}

// Page wrapper styles
// -------------------------

.main-wrapper {
  display: flex;
  min-height: 100%;

  // make sure things like tooltips don't couse the page to scroll
  flex-direction: column;
  overflow: hidden;
}

// Page wrapper styles
// -------------------------

.page {
  flex: 1 1 auto; // fix for bottom footer
}
