.bar-chart__progress {
  position: relative;

  width: 100%;
  height: $chart-bar-thickness;
  margin-bottom: 32px;

  .indicator {
    position: relative;
    float: left;

    width: 100%;
    height: $chart-bar-thickness;
  }

  .indicator-wrapper {
    position: relative;
    float: left;

    &:first-child {
      .indicator {
        border-radius: $chart-bar-thickness 0 0 $chart-bar-thickness;
      }
    }
  }
}

.bar-chart-horizontal {
  position: relative;

  min-width: $chart-bar-min-width;
  margin-bottom: 32px;

  &:last-child {
    margin-bottom: 0;
  }

  color: $color-gray;

  // Limited data chart styles
  // -------------------------

  &--limited {
    padding: 16px 22px 22px;

    background-color: $color-white;
    border-radius: 4px;

    box-shadow: 0 0 4px 0 rgba(0, 0, 0, .15);

    .bar-chart__progress {
      margin: 7px 0 0 0;
      border-radius: $chart-bar-thickness;

      background-color: #d6dee2;
      overflow: hidden;
    }

    .detail-left {
      @include typo-bar-chart-unlimited-title;
      text-align: left;
    }

    .detail-bottom {
      @include typo-body-1;

      display: flex;

      margin-top: 12px;

      .value {
        display: inline-block;
        text-align: right;

        &:not(:first-child) {
          color: $color-gray-mid;

          &::before {
            content: '/ ';
          }

          &::after {
            content: '\00a0 ';
          }
        }
      }

      .unit {
        color: $color-gray-mid;
      }
    }
  }

  // Unlimited data chart styles
  // -------------------------

  &--unlimited {
    padding: 16px 22px 22px;

    background-color: $color-white;
    border-radius: 4px;

    box-shadow: 0 0 4px 0 rgba(0, 0, 0, .15);

    .bar-chart__progress {
      margin: 14px 0 0 0;
    }

    > .bar-chart__detail {
      display: flex;
      justify-content: space-between;

      .detail-left {
        @include typo-bar-chart-unlimited-title;
        text-align: left;
      }

      .detail-right {
        @include typo-body-1;

        display: flex;

        .value {
          display: inline-block;
          text-align: right;

          &::before,
          &::after {
            content: '\00a0 ';
          }
        }

        .unit {
          color: $color-gray-mid;
        }
      }
    }
  }

  // Split data chart styles
  // -------------------------

  &--split {
    border-top: 1px solid $color-chart-divider;

    > .bar-chart__detail {

      display: flex;
      justify-content: space-between;

      margin: 13px 0 18px;
      padding-top: 8px;

      .detail-left {
        @include typo-body-1;

        text-align: left;
      }

      .detail-right {
        @include typo-body-1;

        display: flex;

        .value {
          @include font-semi-bold;

          display: inline-block;
          text-align: right;

          &::before,
          &::after {
            content: '\00a0 ';
          }

          &:nth-child(3) {
            &::before {
              font-weight: normal;

              content: ' / ';
              color: $color-gray-mid;
            }
          }
        }
      }
    }
  }
}

.bar-chart__legend {
  padding-top: 32px;
  border-top: 1px solid $color-chart-divider;
}

.bg--dark {
  .bar-chart-horizontal--split {
    border-top: 1px solid rgba(255, 255, 255, .4);

    .detail-left,
    .detail-right {
      @include font-smoothing;

      color: $color-white;
    }
  }

  .bar-chart__legend {
    border-top: 1px solid rgba(255, 255, 255, .4);
  }
}
