// Select
// --------------------------------------------------

/*doc
---
title: Menus (dropdown)
name: 02-components-13-select-40-overview
category: Components - Menus (dropdown)
---

The select field allows user input through specified options. For the `select` component to be dispalyed properly
make sure to either set the attribute `data-init="auto"` on the component, or the initialize it using the JavaScript API.

```html_example
<form>
  <div class="select">
    <label for="select-1-1">User Name</label>
    <select id="select-1-1" data-init="auto" tabindex="1">
      <option value="" disabled selected>Choose your option 1</option>
      <option value="1">Option 1</option>
      <option value="2">Option 2</option>
      <option value="3" disabled>Option disabled</option>
      <option value="4">Option 4</option>
      <option value="5">Option 5</option>
    </select>
  </div>
  <div class="select">
    <label for="select-1-2">User Name</label>
    <select id="select-1-2" data-init="auto" tabindex="1">
      <option value="" disabled selected>Choose your option 2</option>
      <option value="1">Option 1</option>
      <option value="2">Option 2</option>
    </select>
  </div>
</form>
```

### Disabled state

```html_example
<div class="select">
  <label for="select-2">User Name</label>
  <select id="select-2" data-init="auto" disabled>
    <option value="" disabled selected>Choose your option</option>
    <option value="1">Option 1</option>
    <option value="2">Option 2</option>
  </select>
</div>
```

### Validate

```html_example
<div class="select validate invalid">
  <label for="select-3">User Name</label>
  <select id="select-3" data-init="auto">
    <option value="" disabled selected>Choose your option</option>
    <option value="1">Option 1</option>
    <option value="2">Option 2</option>
  </select>
  <div class="message">
    <i class="icon icon-026-exclamation-mark-circle" aria-hidden="true"></i>
    Error Message
  </div>
</div>
```

### Sample usage

```html_example
<div class="select">
  <select id="select-master">
    <option value="" disabled selected>Select state</option>
    <option value="false">Enabled</option>
    <option value="true">Disabled</option>
  </select>
</div>
<div class="select">
  <select id="select-slave">
    <option value="" disabled selected>Choose your option 4</option>
    <option value="1">Option 1</option>
    <option value="2">Option 2</option>
  </select>
</div>

<script>
document.addEventListener("DOMContentLoaded", function(event) {
  var master = new sdx.Select(document.querySelector("#select-master"));
  var slave = new sdx.Select(document.querySelector("#select-slave"));

  master.addEventListener("change", function(component) {
    slave.disabled = master.value === "true";
  });
});
</script>
```
*/


