// Table styles
// -------------------------

.table {
  display: table;
  width: 100%;

  .table__wrapper {
    display: table;
    width: 100%;
  }

  table {
    display: table;
    width: 100%;

    color: $color-table-text;

    border-collapse: collapse;

    border-top: 1px solid $color-table-border;
    border-bottom: 1px solid $color-table-border;

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  th,
  td {
    @include text-truncate;

    padding-left: 8px;
    padding-right: 8px;

    &:first-child {
      padding-left: 16px;
    }

    &:last-child {
      padding-right: 16px;
    }
  }

  thead {
    @include typo-headline-6;
    @include font-semi-bold;

    tr {
      border-bottom: 1px solid $color-table-border;
      text-align: left;
    }

    th {
      padding-top: 25px;
      padding-bottom: 23px;

      &[data-type] {
        cursor: pointer;
      }

      .arrow-icon {
        @include arrow($color-nav-link);

        display: inline-block;
        visibility: hidden;
        opacity: 0;

        width: 16px;
        height: 16px;

        margin-left: 8px;

        pointer-events: none;
        transform-origin: 50% 50%;

        transition:
          visibility 200ms $standard-easing,
          opacity 200ms $standard-easing;
      }

      &.js-ascending .arrow-icon {
        @include arrow--down;

        visibility: visible;
        opacity: 1;
      }

      &.js-descending .arrow-icon {
        @include arrow--up;

        visibility: visible;
        opacity: 1;
      }
    }
  }

  tbody {
    @include typo-body-2;
    @include font-semi-light;
    @include text-truncate;

    tr {
      text-align: left;

      &:not(:last-child) {
        border-bottom: 1px solid $color-table-border;
      }
    }

    td {
      padding-top: 20px;
      padding-bottom: 17px;
    }
  }

  .button {
    margin: 0 16px;
  }

  // Responsive Table and Reflow Table
  // -------------------------

  &--responsive {
    @include clearfix;

    display: block;

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    .table__wrapper {
      display: block;
      width: 100%;
      min-height: 0;

      overflow-x: auto;
      -ms-overflow-style: -ms-autohiding-scrollbar;

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }

  &--reflow {
    display: block;

    .table__wrapper {
      display: block;
      width: 100%;
    }

    table {
      display: block;
      position: relative;
      width: 100%;
    }

    thead {
      display: block;
      float: left;

      tr {
        display: block;
        border-bottom: 0;

        th {
          padding-left: 16px;

          &::after {
            content: '\00a0';
          }
        }
      }
    }

    tbody {
      display: block;

      width: auto;
      white-space: nowrap;

      overflow-x: auto;
      -ms-overflow-style: -ms-autohiding-scrollbar;

      tr {
        display: table-cell;
        border-bottom: 0;
      }
    }

    thead,
    tbody,
    tfoot {
      tr {
        border-bottom: 0 !important;
      }
    }

    td:empty::after {
      content: '\00a0';
    }

    tr {
      td,
      th {
        display: block;

        padding-top: 20px;
        padding-bottom: 17px;

        border-bottom: 1px solid $color-table-border;

        &:last-child {
          border-bottom: 0;
        }
      }
    }
  }

  // Table styles
  // -------------------------

  &--shadow {
    padding: 19px 0 16px;

    box-shadow: 0 0 4px 0 rgba($color-black, .25);
  }

  &--highlight {
    thead {
      background: $color-table-header-dark;
    }
  }

  &--no-border {
    .table__title {
      padding-left: 0;
      padding-right: 0;
    }

    table {
      th,
      td {
        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
}
