// --------------------------------------------------


$buttons: (
  primary: (
    button-color: $color-button-primary,
    button-color--active: $color-button-primary--active,
    color: $color-white,
    text-opacity: .6
  ),
  secondary: (
    button-color: $color-button-secondary,
    button-color--active: $color-button-secondary--active,
    color: $color-gray,
    text-opacity: .4
  ),
  confirm: (
    button-color: $color-button-confirm,
    button-color--active: $color-button-confirm--active,
    color: $color-white,
    text-opacity: .6
  )
);

// --------------------------------------------------


// Global Button styles
// --------------------------------------------------

button,
.button {
  cursor: pointer;
}


// Button Normal
// --------------------------------------------------

.button {
  @include typo-body-1;
  @include text-truncate;
  @include font-smoothing;

  display: inline-block;

  position: relative;

  transition: all 70ms $button-easing;

  margin: 0;

  outline: none;
  border: 0;
  border-bottom-style: none;
  border-radius: $button-border-radius;
  background: none;
  cursor: pointer;

  padding:
  $button-padding-vertical
  $button-padding-horizontal;

  min-width: 160px;

  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  text-shadow: 0 0 0;

  transform-origin: 50% 50%;

  user-select: none;

  // Active styles
  // -------------------------

  &:active {
    transform: scale(.98);
  }

  // Disabled styles
  // -------------------------

  &.disabled,
  &:disabled {
    cursor: not-allowed;
    pointer-events: none;
  }

  @each $button, $settings in $buttons {
    @if $button == primary {
      &,
      &--#{$button} {
        @include button($settings);
      }
    } @else {
      &--#{$button} {
        @include button($settings);
      }
    }
  }

  // Icon styles
  // -------------------------

  &__icon {
    padding: 12px 24px 12px 16px;
  }

  .icon {
    margin-right: 7px;
    font-size: 18px;
  }
}


// Button Thin
// --------------------------------------------------

.button--thin,
%button--thin {
  $color: $color-white;

  // -------------------------

  padding:
  $button-padding-vertical - $button-border-width
  $button-padding-horizontal - $button-border-width;

  @each $button, $settings in $buttons {
    @if $button == primary {
      &,
      &.button--#{$button} {
        @include button-thin($settings, $color-white);
      }
    } @else {
      &.button--#{$button} {
        @include button-thin($settings, $color-white);
      }
    }
  }
}


// Dark Background
// --------------------------------------------------

.bg--dark {
  .button {
    @extend %button--thin;
  }
}


// Full button
// --------------------------------------------------

.button--full,
%button--full {
  display: block;
  width: 100%;

  &:active {
    transform: scale(.9925, .98);
  }
}


// Responsive button
// --------------------------------------------------

.button--responsive,
%button--responsive {
  width: 100%;

  @include bp-sm {
    width: auto;
  }
}


// Specific overrides
// --------------------------------------------------

input[type='submit'],
input[type='reset'],
input[type='button'] {
  &.button--full {
    @extend %button--full;
  }

  &.button--responsive {
    @extend %button--responsive;
  }
}
