title: Overview
name: 02-components-22-notification-header-01-overview
category: Components - Notification Header
---

Notification headers are coloured strips which run across the top of a product, beneath the header bar or navigation.

#### How to use notification headers
Notifications headers are used to give users in page information about something new, or to confirm an action thats just been made by the user. They do this in a discrete non-disruptive way. Notifications should always use the SDX interaction colour palette to help explain the type of notification the user is receiving at a glance. They run the full width of the page.
