// Radio Buttons
// --------------------------------------------------

/*doc
---
title: Radio Buttons
name: 02-components-04-form-30-radiobuttons
category: Components - Radio Buttons
---

Use of the radio button element.

```html_example
<form>
  <div class="radio">
    <input type='radio' name='dummy' id='radio1-1'>
    <label for='radio1-1'>
      Option 1<br/>
      MuliLine Option 3/4<br/>
      (CHF 9.00/month)
    </label>
  </div>
  <div class="radio">
    <input type='radio' name='dummy' id='radio1-2'>
    <label for='radio1-2'>Option 2</label>
  </div>
</form>
```

### Disabled States

```html_example
<form>
  <div class="radio">
    <input type='radio' name='dummy' id='radio2-1' disabled>
    <label for='radio2-1'>Option 1</label>
  </div>
  <div class="radio">
    <input type='radio' name='dummy' id='radio2-2' checked disabled>
    <label for='radio2-2'>Option 2</label>
  </div>
</form>
```

### Vertical distance between radio buttons

Add the class `add-space` to the `div` element to increase the vertical distance between radio buttons.

```html_example
<form>
  <div class="radio">
    <input type='radio' name='dummy' id='radio4-1'>
    <label for='radio4-1'>Option 1</label>
  </div>
  <div class="radio add-space">
    <input type='radio' name='dummy' id='radio4-2'>
    <label for='radio4-2'>Option 2</label>
  </div>
  <div class="radio">
    <input type='radio' name='dummy' id='radio4-3' checked>
    <label for='radio4-3'>Option 3</label>
  </div>
  <div class="radio">
    <input type='radio' name='dummy' id='radio4-4'>
    <label for='radio4-4'>Option 4</label>
  </div>
</form>
```

### Inline radio buttons

Add the class `form-inline` to a wrapper `div` element to set the checkboxes to inline.

```html_example
<form>
  <div class="form-inline">
    <div class="radio">
      <input type='radio' name='dummy' id='radio5-1'>
      <label for='radio5-1'>Option 1</label>
    </div>
    <div class="radio">
      <input type='radio' name='dummy' id='radio5-2'>
      <label for='radio5-2'>Option 2</label>
    </div>
  </div>
</form>
```

## Grouping radio buttons

