```html_example
<form>
  <div class="form-group" role="radiogroup">
    <label>Radio group</label>
    <div class="radio">
      <input type='radio' name='dummy' id='radio6-1'>
      <label for='radio6-1'>Option 1</label>
    </div>
    <div class="radio">
      <input type='radio' name='dummy' id='radio6-2'>
      <label for='radio6-2'>Option 2</label>
    </div>
  </div>
</form>
```

### Inline (Horizontal) radio button group

```html_example
<form>
  <div class="form-group form-inline" role="radiogroup">
    <label>Radio group</label>
    <div class="radio">
      <input type='radio' name='dummy' id='radio7-1'>
      <label for='radio7-1'>Option 1</label>
    </div>
    <div class="radio">
