.accordion {

  // Accordion Styles
  // -------------------------

  &__item {
    position: relative;
  }

  &__toggle {
    display: block;

    margin: 0;
    padding: 0;
    border: 0;

    width: 100%;

    cursor: pointer;

    .arrow-icon {
      display: inline-block;

      float: right;

      pointer-events: none;
      transform-origin: 50% 50%;
    }
  }

  &__header {
    display: inline-block;
    position: relative;
  }

  &__collapse {
    position: relative;
    overflow: hidden;

    display: none;
    height: 0;
    width: 100%;

    opacity: 1;

    &.is-open {
      display: block;
      height: 100%;
      width: 100%;
    }
  }

  // Accordion Arrow Styles
  // -------------------------

  &,
  &--arrow-flat {
    @include accordion-arrow-flat($color-accordion, $color-accordion--active);
  }

  &--arrow-normal {
    @include accordion-arrow-normal($color-accordion, $color-accordion--active);
  }
}
