/*doc
---
title: Icons
name: 01-foundation-03-icons
category: Foundation - Icons
---

The default SDX icon set definition.

## How to use the icons
To use these icons, use the `icon` class on an `i` element and provide the ligature as class name.

```html_example
<i class="icon icon-account" aria-hidden="true"></i>
```

## Size & Display
To control the size of the icon, change the `font-size` property of your icon. Optionally you can use preset classes as shown below.

```html_example
<i class="icon icon-account icon--s1" aria-hidden="true"></i>
<i class="icon icon-account icon--s2" aria-hidden="true"></i>
<i class="icon icon-account icon--s3" aria-hidden="true"></i>
<i class="icon icon-account icon--s4" aria-hidden="true"></i>
<i class="icon icon-account icon--s5" aria-hidden="true"></i>
<i class="icon icon-account icon--s6" aria-hidden="true"></i>
```

To flip the icon, use the `icon--flip-horizontal`, `icon--flip-vertical` or `icon--flip-horizontal-vertical` classes as shown below.

```html_example
<i class="icon icon-search icon--s3" aria-hidden="true"></i>
<i class="icon icon-search icon--flip-horizontal icon--s3" aria-hidden="true"></i>
<i class="icon icon-search icon--flip-vertical icon--s3" aria-hidden="true"></i>
<i class="icon icon-search icon--flip-horizontal-vertical icon--s3" aria-hidden="true"></i>
```

