Badges are small and subtle notification indicators used to provide users with small pieces of information, without disrupting their experience.

# How to use badges
They can be used to indicate unread emails, notifications, changes to settings, and even prompt user
actions such as refreshing as a page to reveal new content. They should always feature one of the three interaction colours,
helping to clarify the type and importance of the notification/alert.

