// --------------------------------------------------


.badge {
  display: inline-block;
  position: relative;

  // Base Styles
  // -------------------------

  &__content {
    @include font-sans;
    @include font-light;

    display: inline-block;

    position: relative;

    transition: all 200ms $standard-easing;

    border-radius: 24px;
    background: $color-int-blue;
    cursor: pointer;

    height: 24px;

    text-align: center;
    line-height: 1;

    color: $color-white;

    font-size: 16px;
    transform-origin: 50% 50%;

    user-select: none;

    backface-visibility: hidden;

    &:hover {
      background: $color-int-blue--active;
    }
  }

  // Massage Styles
  // -------------------------

  &--message {
    display: flex;

    height: 24px;

    justify-content: center;

    .badge__content {
      animation: badge-message 600ms linear both;

      padding: 0;
      max-width: $container-max-width;

      overflow: hidden;

      transform-origin: 50% 50%;
      backface-visibility: hidden;

    }

    .badge__text {
      @include font-smoothing;
      @include text-truncate;

      display: block;

      animation: badge-message-text 600ms linear both;

      padding: 4px 12px 2px;
      height: 100%;

      letter-spacing: -.6px;
      justify-content: center;

      backface-visibility: hidden;
    }
  }

  // Badge Styles
  // -------------------------

  &--icon,
  &--avatar {
    // default Avatar size
    .avatar {
      width: 32px;
      height: 32px;
    }

    .badge__content {
      position: absolute;
      top: 0;
      left: calc(100% - 9px);

      animation: badge-icon 500ms linear both;

      padding: 2px 5px;

      min-width: 16px;
      height: 16px;

      font-size: 12px;

      backface-visibility: hidden;

      &--icon {
        padding: 0;

        width: 16px;
        height: 16px;

        .icon {
          @include default-font-smoothing;

          width: 16px;

          text-align: center;
          line-height: 16px;
          letter-spacing: normal;

          font-size: 12px;
        }

        .icon-heart {
          font-size: 8px;

          &::before {
            content: icon-char(195-heart-filled);
          }
        }

        .icon-check::before {
          content: icon-char(011-check-mark);
        }

        .icon-alert::before {
          content: icon-char(196-exclamation-mark);
        }
      }
    }
  }

  // Badge Color
  // -------------------------

  &--alert,
  &--notification {
    .badge__content {
      background: $color-int-orange;

      &:hover {
        background: $color-int-orange--active;
      }
    }
  }

  &--confirmation {
    .badge__content {
      background: $color-int-green;

      &:hover {
        background: $color-int-green--active;
      }
    }
  }
}

// Badge Message Animation
// --------------------------------------------------

@keyframes badge-message {
  0% {
    transform: scale(0);
    max-width: 24px;
  }

  30% {
    transform: scale(1);
    max-width: 24px;
  }

  75% {
    max-width: 272px;
  }
}

@keyframes badge-message-text {
  0% {
    opacity: 0;
  }

  75% {
    transform: scale(.95);
    opacity: 0;
    backface-visibility: hidden;
  }

  100% {
    transform: scale(1);
    opacity: 1;
    backface-visibility: hidden;
  }
}

// Badge Icon Animation
// --------------------------------------------------

@keyframes badge-icon {
  0% {
