Use of the Input field element.

```html_example
<form>
  <div class="input-field" data-init="auto">
    <input type="text" id="user-name-1" placeholder="User Name">
    <label for="user-name-1">User Name</label>
  </div>
  <div class="input-field" data-init="auto">
    <input type="email" id="email-1" placeholder="Email">
    <label for="email-1">Email</label>
  </div>
  <div class="input-field" data-init="auto">
    <input type="password" id="password-1" placeholder="Password">
    <label for="password-1">Password</label>
  </div>
  <div class="input-field input-multiline" data-init="auto">
    <textarea id="multiline-1" placeholder="Multiline text" data-min-rows="3" data-max-rows="10"></textarea>
    <label for="multiline-1">Multiline text</label>
  </div>
</form>
```

### Fixed Height

Adding the class `input-field--fixed` to the `input-field` element will prevent the input field to animate the formfield, when it has focus.

```html_example
<form>
  <div class="input-field input-field--fixed" data-init="auto">
    <input type="text" id="user-name-2" placeholder="User Name">
    <label for="user-name-2">User Name</label>
  </div>
  <div class="input-field input-field--fixed" data-init="auto">
    <input type="email" id="email-2" placeholder="Email">
    <label for="email-2">Email</label>
  </div>
  <div class="input-field input-field--fixed" data-init="auto">
    <input type="password" id="password-2" placeholder="Password">
    <label for="password-2">Password</label>
  </div>
  <div class="input-field input-multiline input-field--fixed" data-init="auto">
    <textarea id="multiline-2" placeholder="Multiline text" data-min-rows="3" data-max-rows="10"></textarea>
    <label for="multiline-2">Multiline text</label>
  </div>
</form>
```

### Read-only and disabled input fields.

If you want to block the user from changing an input field add the class `readonly` or `disabled` to the input element.

The difference between the `readonly` and `disabled` attribute is that in `readonly` the user can tab to the element and copy the text from the input field.

```html_example
<form>
  <div class="input-field" data-init="auto">
    <input type="text" id="readonly" placeholder="Read-only" value="This text is read-only" readonly>
    <label for="readonly">Read-only</label>
  </div>
  <div class="input-field" data-init="auto">
    <input type="text" id="disabled" placeholder="Disabled" value="This text is disabled" disabled>
    <label for="disabled">Disabled</label>
  </div>
  <div class="input-field input-multiline" data-init="auto">
    <textarea id="multiline-readonly-1" placeholder="Read-only (Multiline)" readonly data-max-rows="3">This text is read-only and it is more than one line long. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</textarea>
    <label for="multiline-readonly-1">Read-only (Multiline)</label>
  </div>
  <div class="input-field input-multiline" data-init="auto">
    <textarea id="multiline-disabled-1" placeholder="Multiline Disabled" disabled data-max-rows="10">This text is read-only and it is more than one line long. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</textarea>
    <label for="multiline-disabled-1">Disabled (Multiline)</label>
  </div>
</form>
```

### Validate input fields

Add the class `validate` to the input element to validate the field. The `validate` class leverages HTML5 validation and will add
a `valid` and `invalid` class accordingly. If you don't want the valid and invalid states, just remove the validate class from your inputs.

If you have custom validations on a field add the `valid` or `invalid` class to the input element depending on your validation result.

```html_example
<form>
  <div class="input-field" data-init="auto">
    <input type="text" id="user-name-3" placeholder="User Name" value="Valid input" class="validate valid">
    <label for="user-name-3">User Name</label>
    <i class="icon icon-011-check-mark" aria-hidden="true"></i>
  </div>

  <div class="input-field" data-init="auto">
    <input type="text" id="user-name-4" placeholder="User Name" value="Invalid input (inline)" class="validate invalid invalid--inline">
    <label for="user-name-4">Name</label>
    <i class="icon icon-026-exclamation-mark-circle" aria-hidden="true"></i>
    <div class="message">
      <i class="icon icon-026-exclamation-mark-circle" aria-hidden="true"></i>
      Error Message
    </div>
  </div>

  <div class="input-field" data-init="auto">
    <input type="email" id="email-3" placeholder="Email" value="Invalid input with message" class="validate invalid">
    <label for="email-3">Email</label>
    <div class="message">
      <i class="icon icon-026-exclamation-mark-circle" aria-hidden="true"></i>
      Error Message
    </div>
  </div>

  <div class="input-field" data-init="auto">
    <input type="text" id="user-name-5" placeholder="User Name" value="Invalid input (inline)" class="validate invalid invalid--inline">
    <label for="user-name-5">Name</label>
    <div class="message">
      <i class="icon icon-026-exclamation-mark-circle" aria-hidden="true"></i>
      Error Message
    </div>
  </div>
</form>
```

### Autocomplete input fields
An input field with autocomplete-dropdown support. JavaScript documentation can be found [here](/doc/Autocomplete.html)

**Note:** To test this field enter a programming language, or just type *script*

```html_example
<script>
document.addEventListener("DOMContentLoaded", function(event) {
  var config = {
    minChars: 2,
    source: function(term, suggest) {
      term = term.toLowerCase();
      let choices = ["ActionScript", "AppleScript", "Asp", "Assembly", "BASIC", "Batch", "C",
        "C++", "CSS", "Clojure", "COBOL", "ColdFusion", "Erlang", "Fortran", "Groovy", "Haskell",
        "HTML", "Java", "JavaScript", "Lisp", "Perl", "PHP", "PowerShell", "Python", "Ruby",
        "Scala", "Scheme", "SQL", "TeX", "XML", "AnyScript & OtherScript"];
      var matches = [];
      for (var i = 0; i < choices.length; i++) {
        if (choices[i].toLowerCase().indexOf(term) >= 0) {
          matches.push(choices[i]);
        }
      }
      suggest(matches, term);
    }
  };

  var element = document.getElementById("autocomplete-1");
  var autocomplete = new sdx.Autocomplete(element, config);
  var input = new sdx.InputField(element.querySelector("input"))
});
</script>

<form onsubmit="return false;">
  <div class="input-field input-field--autocomplete" id="autocomplete-1">
    <input type="text" id="langs" placeholder="Programming languages ..." value="">
    <label for="langs">Programming languages</label>
    <div class="autocomplete js-autocomplete">
    </div>
    <div class="message">
      <i class="icon icon-026-exclamation-mark-circle" aria-hidden="true"></i>
