// Extends
// -------------------------

@mixin label-fixed {
  margin-top: 29px;

  + label {
    transform: translateY(0);
    opacity: 1;
  }
}

// --------------------------------------------------


// Text input styles
// -------------------------

input,
textarea {
  -webkit-border-radius: 0;

  &::-ms-clear {
    display: none;
  }

  @keyframes onAutoFillStart {
    from {
      /* Empty */
    }
    to {
      /* Empty */
    }
  }

  &:-webkit-autofill {
    // Expose a hook for JavaScript when auto fill is shown.
    // JavaScript can capture 'animationstart' events
    animation-name: onAutoFillStart;

    // Make the backgound color become yellow _really slowly_
    transition: background-color 50000s ease-in-out 0s;
  }
}


// Input field wrapper styles
// -------------------------

.input-field {
  position: relative;

  margin-bottom: 11px;
  padding: 0;

  .message {
    max-height: 0;
    margin-top: 0;

    opacity: 0;
    z-index: $zindex-form + 1;

    transition: 150ms all $standard-easing;
  }

  .icon {
    margin-right: $form-input-icon-margin;
  }

  &:first-child {
    input:not([type]),
    input[type='text'],
    input[type='password'],
    input[type='email'],
    input[type='url'],
    input[type='time'],
    input[type='date'],
    input[type='datetime'],
    input[type='datetime-local'],
    input[type='tel'],
    input[type='number'],
    input[type='search'],
    textarea {
      margin-top: 0;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  // Text input styles
  // -------------------------

  input,
  textarea {
    -webkit-border-radius: 0;

    &::-ms-clear {
      display: none;
    }
  }

  input:not([type]),
  input[type='text'],
  input[type='password'],
  input[type='email'],
  input[type='url'],
  input[type='time'],
  input[type='date'],
  input[type='datetime'],
  input[type='datetime-local'],
  input[type='tel'],
  input[type='number'],
  input[type='search'],
  textarea {
    @include typo-body-1;

    position: relative;

    transition: all 150ms $standard-easing;
    z-index: $zindex-form;

    margin-top: 13px;

    outline: none;

    border: $border-width solid $color-input-normal-form;
    border-radius: $border-radius;

    background-color: $color-input-bg;

    padding: 0 16px;

    width: 100%;
    height: $form-input-height;

    color: $color-input-normal-text;

    font-weight: 300;

    user-select: text;
    backface-visibility: hidden;

    + label {
      position: absolute;
      top: 0;
      left: 0;

      transform: translateY(24px);
      transition: 150ms all $standard-easing;

      opacity: 0;

      cursor: text;

      color: $color-input-label;
      font-size: 14px;
    }

    // icon
    ~ .icon {
      display: block;

      position: absolute;
      right: 0;
      bottom: 0;

      opacity: 0;
      z-index: $zindex-form + 1;

      width: $form-input-height;
      height: $form-input-height;

      text-align: center;
      line-height: $form-input-height;

      color: $color-input-success-icon;
      font-size: 22px;
    }

    &::placeholder {
      transition: 150ms opacity $standard-easing;

      opacity: 1;
      color: $color-input-placeholder;
    }

    // Focused input styles
    // -------------------------

    &:focus:not([readonly]) {
      @include label-fixed;

      border-color: $color-input-selected-form;
      color: $color-input-selected-text;

      &::placeholder {
        opacity: 0;
      }
    }

    // Disabled and read-only input styles
    // -------------------------

    &:disabled,
    &[readonly] {
      // iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655.
      opacity: 1;

      border-color: $color-input-disabled-form;
      background-color: $color-input-disabled-bg;
      color: $color-input-disabled-text;

      + label {
        color: $color-input-label;
      }
    }

    &:disabled {
      cursor: not-allowed;
      user-select: none;
    }

    // Validation Input Style
    // -------------------------

    &.valid,
    &:valid,
    &.invalid,
    &:invalid {
      box-shadow: none;

      background-repeat: no-repeat;
      background-position: center right 16px;
      background-size: 22px 22px;

      ~ .icon {
        display: none;
      }
    }

    // Valid Input Style
    // -------------------------

    &.valid {
      border-color: $color-input-success-form;

      padding-right: $form-input-height + $form-input-icon-margin;
      color: $color-input-success-text;

      @include background-svg($icon-ok);
    }

    // Invalid Input Style
    // -------------------------

    &.invalid {
      border-color: $color-input-invalid-form;
      color: $color-input-invalid-text;

      &--inline {
        padding-right: $form-input-height + $form-input-icon-margin;

        @include background-svg($icon-error);
      }

      ~ .message {
        max-height: 50px;
        margin-top: 6px;

        opacity: 1;

        color: $color-input-invalid-text;
      }
    }

    // Full width label when using validate for error messages
    &.validate + label {
      width: 100%;
      pointer-events: none;
    }

    // Custom classes
    // -------------------------

    &.is-fixed {
      @include label-fixed;
    }
  }

  // Fixed Height Input Field
  // --------------------------------------------------

  &--fixed {
    margin: 0 0 4px;

    input:not([type]),
    input[type='text'],
    input[type='password'],
    input[type='email'],
    input[type='url'],
    input[type='time'],
    input[type='date'],
    input[type='datetime'],
    input[type='datetime-local'],
    input[type='tel'],
    input[type='number'],
    input[type='search'],
    textarea {
      margin-top: 29px;
    }
  }

  // Autocomplete styles
  // --------------------------------------------------

  &--autocomplete {
    .autocomplete {
      position: absolute;

      transform: scale(1, 0);
      transform-origin: 50% 0;
      transition: all 200ms $standard-easing;

      opacity: .2;

      width: 100%;
      height: auto;

      margin-top: -1px;

      z-index: $zindex-form + 1;

      cursor: pointer;
      overflow: hidden;

      background: $color-input-autocomplete-bg;
      box-shadow: 0 0 4px 0 rgba($color-black, .2);

      color: $color-input-autocomplete-text;

      strong {
        @include font-semi-light;
        color: $color-search-live-text-match;
      }

      li {
        &.js-hover,
        &:hover {
          background: $color-input-autocomplete-bg-hover;
        }

        .autocomplete__result {
          margin: 0 22px;
          padding: 12px 0;
        }

        &:not(:last-child) {
          .autocomplete__result {
            border-bottom: 1px solid $color-aluminum-80;
          }
        }
      }
    }

    &.is-open {
      input:not([type]),
      input[type='text'],
      input[type='password'],
      input[type='email'],
      input[type='url'],
      input[type='time'],
      input[type='date'],
      input[type='datetime'],
      input[type='datetime-local'],
      input[type='tel'],
      input[type='number'],
      input[type='search'],
      textarea {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }

      .autocomplete {
        transform: scale(1, 1);

        opacity: 1;
        z-index: $zindex-topmost;
      }
    }
  }
}

// Textarea styles
// -------------------------

.input-multiline, // DEPRECATED
.input-field--multiline {
  textarea {
    width: 100%;
    height: auto;

    padding: 11px 16px;

    resize: none;
    box-sizing: border-box;
  }
}

