// Core Colors
// --------------------------------------------------

.white {
  color: $color-white;
}

.navy {
  color: $color-navy;
}

.blue {
  color: $color-blue;

  &-40 {
    color: $color-blue-40;
  }

  &-20 {
    color: $color-blue-40;
  }
}

.aluminum {
  color: $color-aluminum;

  &-80 {
    color: $color-aluminum-80;
  }
}

.horizon {
  color: $color-horizon;

  &-40 {
    color: $color-horizon-40;
  }
}

.gray {
  color: $color-gray;

  &-90 {
    color: $color-gray-90;
  }

  &-80 {
    color: $color-gray-80;
  }

  &-60 {
    color: $color-gray-60;
  }

  &-40 {
    color: $color-gray-40;
  }

  &-20 {
    color: $color-gray-20;
  }

  &-mid {
    color: $color-gray-mid;
  }

  &-light {
    color: $color-gray-light;
  }
}


// Brand Colors
// --------------------------------------------------

.sc {
  &-blue {
    color: $color-sc-blue;
  }

  &-red {
    color: $color-sc-red;
  }

  &-purple {
    color: $color-sc-purple;
  }

  &-navy {
    color: $color-sc-navy;
  }

  &-white {
    color: $color-sc-white;
  }

  &-gray {
    color: $color-sc-gray;

    &-mid {
      color: $color-sc-gray-mid;
    }

    &-light {
      color: $color-sc-gray-light;
    }
  }
}


// Interaction Colors
// --------------------------------------------------

.int {
  &-blue {
    color: $color-int-blue;

    &--active {
      color: $color-int-blue--active;
    }
  }

  &-gray {
    color: $color-int-gray;

    &--active {
      color: $color-int-gray--active;
    }
  }

  &-green {
    color: $color-int-green;

    &--active {
      color: $color-int-green--active;
    }
  }

  &-red {
    color: $color-int-red;

    &--active {
      color: $color-int-red--active;
    }
  }

  &-orange {
    color: $color-int-orange;

    &--active {
      color: $color-int-orange--active;
    }
  }
}


// Content Colors
// --------------------------------------------------

.orchid {
  color: $color-orchid;

  &--active {
    color: $color-orchid--active;
  }
}

.iris {
  color: $color-iris;

  &--active {
    color: $color-iris--active;
  }
}

.pink {
  color: $color-pink;

  &--active {
    color: $color-pink--active;
  }
}

.turquoise {
  color: $color-turquoise;

  &--active {
    color: $color-turquoise--active;
  }
}

.apple {
  color: $color-apple;

  &--active {
    color: $color-apple--active;
  }
}

.petrol {
  color: $color-petrol;

  &--active {
    color: $color-petrol--active;
  }
}
