  }

  .exampleOutput .bg {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    flex: 1 1 auto;
  }

  .exampleOutput .card--upload-photos {
    max-width: 640px;
  }

  .exampleOutput .card--upload-files {
    max-width: 552px;
  }

  .exampleOutput .card--document {
    max-width: 420px;
  }

  @media only screen and (min-width: 768px) {
    .exampleOutput .bg {
      flex: 1 1 270px;
      margin: 0;
    }

    .exampleOutput .card {
      flex: 1 1 270px;
    }
  }
</style>

Cards are used to group and smaller pieces of contain content in page, and give them extra emphasis. Cards are colour coded based on the content theyre surfacing.

# How to use cards
Use cards to give content clearer separation from the rest of the content in page. With the addition of colour labelling, cards also helps to categorise content and makes wayfinding throughout products clearer.

# 8 column grid
All cards are built on an 8 column grid structure that helps make them flexible across different grid sizes. Content placed inside a card can change in size and padding to fit the variations in margin width and column width of the grid for the page that they're place inside.
Use class `row` and classes `col-1` to `col-8` for the card grid.
