title: Clickable Cards
name: 02-components-07-cards-02-clickable
category: Components - Cards
---

Cards are by default not clickable. To make a card clickable add the class `card--clickable` to the card.

```html_example
<div class="card card--dark card--notification">
  <div class="card__header">
    <h1 class="align-left">Data</h1>
    <h2 class="align-right">Today 13:21</h2>
  </div>
  <p class="card__text">Global roaming is now switched on</p>
</div>
<div class="card card--dark card--notification card--clickable">
  <div class="card__header">
    <h1 class="align-left">Data</h1>
    <h2 class="align-right">Today 13:21</h2>
  </div>
  <p class="card__text">Global roaming is now switched on</p>
</div>
```
