  </div>
</div>
```
*/

// --------------------------------------------------


.card {

  // Setup 8 column grid system in Cards
  // -------------------------

  .row {
    @include make-card-row($card-grid-gutter);
  }

  @include make-card-columns($card-grid-columns, $card-grid-gutter);


  // Base Styles
  // -------------------------

  position: relative;

  width: 100%;

  margin-left: auto;
  margin-right: auto;

  padding: 30px 24px;

  background: $color-card-bg;

  color: $color-gray;

  overflow: hidden;

  transition: background 200ms $standard-easing;

  &::before {
    content: '';

    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 4px;

    background: $color-card-navy;
  }

  .card__header {
    @include typo-headline-5;
    @include font-semi-light;
  }

  .card__footer {
    padding: 24px;
  }

  .card__text {
    @include typo-body-1;
    margin: 0;
  }

  .link::before {
    content: '';
    display: block;
    margin-top: 4px;
  }


  // Card Variant Styles
  // -------------------------

  &--dark {
    background: $color-card-gray-bg;
  }


  // Clickable Card Styles
  // -------------------------

  &.card--clickable {
    cursor: pointer;

    &:hover {
      background: $color-card-bg--active;

      &.card--dark {
        background: $color-card-gray-bg--active;
      }
    }
  }


  // Card Styles
  // --------------------------------------------------

  // Notification Styles
  // -------------------------

  &--notification {
    padding: 12px 16px 20px;

    border-radius: $card-border;

    .card__header {
      @include clearfix;

      h1,
      h2 {
        @include typo-small;
        margin-bottom: 4px;
      }

      h1 {
        @include font-semi-bold;
      }

      h2 {
        @include font-semi-light;
        color: $color-gray-mid;
      }
    }
  }


  // Image Styles
  // -------------------------

  &--image-top,
  &--image-bottom {
    padding: 0;

    &::before {
      content: none;
    }

    .card__image {
      position: relative;

      width: 100%;
      height: $card-image-height;

      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;

      overflow: hidden;

      // filter would be the nice solution, but IE 10 and 11 is not supporting it :(
      // so we use a ::before element with a black background
      //filter: brightness(1);

      &::before {
        content: '';

        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        background: rgba($color-black, .1);

        opacity: 0;

        transition: opacity 200ms $standard-easing;
      }
    }

    .card__body {
      position: relative;

      padding: 12px 16px 20px;

      &::before {
        content: '';

        position: absolute;
        top: 0;
        left: 0;

        width: 100%;
        height: 4px;

        background: $color-card-navy;
      }
    }
  }

  &--image-top {
    .card__image::before {
      border-radius: $card-border $card-border 0 0;
    }

    &.card--clickable {
      &:hover {
        // filter would be the nice solution, but IE 10 and 11 is not supporting it :(
        //.card__image {
        //  filter: brightness(.9);
        //}

        .card__image::before {
          opacity: 1;
        }
      }
    }
  }

  &--image-bottom {
    .card__image::before {
      border-radius: 0 0 $card-border $card-border;
    }

    &.card--clickable {
      &:hover {
        // filter would be the nice solution, but IE 10 and 11 is not supporting it :(
        //.card__image {
        //  filter: brightness(.9);
        //}

        .card__image::before {
          opacity: 1;
        }
      }
    }
  }

  // Promo Styles
  // -------------------------

  &--promo {
    .card__image {
      display: block;

      max-width: 100%;

      margin: 0 auto;

      opacity: 1;

      transition: opacity 200ms $standard-easing;
    }

    &.card--clickable {
      &:hover {
        .card__image {
          // filter would be the nice solution, but IE 10 and 11 is not supporting it :(
          // so we use opacity to fade the image slightly
          // filter: brightness(.9);

          opacity: .95;
        }
      }
    }
  }


  // Upload Styles
  // -------------------------

  &--upload-photos {
    padding-top: 23px;
    padding-bottom: 24px;

    border-radius: 0;

    .card__header {
      padding-bottom: 25px;
    }

    .card__body {
      display: flex;
      overflow: hidden;
    }

    .card__image {
      &:not(:last-child) {
        margin-right: 20px;
      }

      img {
        display: block;

        width: auto;
        height: 103px;
      }
    }
  }

  &--upload-files {
    padding: 0;

    border-radius: 0;

    .card__header {
      padding: 25px 24px 21px;
      border-bottom: 1px solid $color-aluminum;
    }

    .card__item {
      @include typo-body-1;

      padding: 21px 24px 22px;
      align-items: center;

      &:not(:last-child) {
        border-bottom: 1px solid $color-aluminum;
      }

      .card__item-icon {
        margin-right: 6px;

        float: left;
        font-size: 30px;
      }

      .card__text {
        @include text-truncate;
      }
    }
  }

  // Document Styles
  // -------------------------

  &--document {
    padding: 0;

    border-radius: 0;

    a {
      margin: 0;
      color: inherit;
      border-bottom: inherit;

      .card__item-detail {
        color: $color-int-blue;
      }

      &:hover {
        .card__item-detail {
          color: $color-int-blue--active;
        }
      }
    }

    .card__header {
      @include typo-headline-4;
      @include font-light;

      padding: 22px 24px 17px;
      border-bottom: 1px solid $color-aluminum;
    }

    .card__footer {
      border-top: 1px solid $color-aluminum;

      text-align: center;
    }

    .card__item {
      @include typo-body-1;

      display: block;

      padding: 15px 24px 16px;
      align-items: center;

      &:not(:last-child) {
        border-bottom: 1px solid $color-aluminum;
      }

      .card__item-icon {
        margin-top: 1px;
        margin-right: 6px;

        float: left;
        font-size: 30px;
      }

      .card__item-detail {
        font-size: 21px;

        .icon {
          @include font-semi-bold;
        }
      }

      .card__item-wrapper {
        overflow: hidden;
      }

      h1,
      p {
        @include text-truncate;
        margin: 0;
      }

      h1 {
        @include font-semi-bold;

        line-height: 22px;
        letter-spacing: 0;
        font-size: 16px;
      }

      p {
        line-height: normal;
        letter-spacing: .1px;
        font-size: 14px;

        color: $color-gray-mid;
      }
    }

    // Travel Styles
    // -------------------------

    .card__travel {
      &-station {
        h1 {
          @include typo-headline-4;
          @include font-light;

          color: $color-gray-mid;
        }

        p {
          @include typo-body-1;
          @include font-light;

          color: $color-gray-mid;

          strong {
            @include font-semi-bold;
            color: $color-gray;
          }
        }
      }

      &-time {
        h1 {
          @include typo-headline-4;
          @include font-light;

          color: $color-gray;
        }

        p {
          @include typo-body-2;
          @include font-semi-light;

          color: $color-gray-mid;
        }
      }

      &-direction {
        h1 {
          @include typo-body-2;
          @include font-semi-bold;

          color: $color-gray;
        }

        p {
          @include typo-body-2;
          @include font-semi-light;

          color: $color-gray-mid;
        }
      }
    }
  }

  // News Styles
  // -------------------------

  &--news {
    padding: 0;

    &::before {
      content: none;
    }

    .card__image {
      position: relative;

      width: 100%;
      height: $card-image-news-height;

      background-size: cover;
      background-repeat: no-repeat;
      background-position: center top;

      overflow: hidden;
    }

    .card__header {
      @include clearfix;

      h2,
      p,
      a {
        @include typo-body-2;
      }
    }

    .card__body {
      position: relative;

      padding: 7px 16px 12px;

      .card__title {
        @include typo-headline-4;
        @include font-semi-light;

        margin-bottom: 5px;
      }

      .card__text {
        @include typo-body-2;
        @include font-semi-light;
      }
    }

    .card__tray {
      width: 100%;
      margin-top: 20px;

      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    .tray-item {
      position: relative;
      margin-right: 30px;

      .icon {
        color: $color-int-blue;
        font-size: 24px;
      }

      .count {
        @include font-semi-light;

        position: absolute;
        top: -4px;

        font-size: 12px;
        color: $color-gray;
      }
    }
  }

  // News Dark Style
  // -------------------------

  &--dark {
    &.card--news {
      @include font-smoothing;

      background: $color-navy;
      color: $color-white;

      .tray-item {
        .count {
          color: $color-int-blue;
        }
      }
    }
  }

  // Border Radius Option
  // -------------------------

  &--rounded {
    border-radius: $card-border;
  }

  &--not-rounded {
    &,
    .card__image::before {
      border-radius: 0;
    }
  }


  // Card Color
  // -------------------------

  &--navy::before {
    background: $color-card-navy;
  }

  &--blue::before {
    background: $color-card-blue;
  }

  &--purple::before {
    background: $color-card-purple;
  }

  &--turquoise::before {
    background: $color-card-turquoise;
  }

  &--apple::before {
    background: $color-card-apple;
  }

  &--iris::before {
    background: $color-card-iris;
  }

  &--petrol::before {
    background: $color-card-petrol;
  }

  &--pink::before {
    background: $color-card-pink;
  }
}
