/*doc
---
title: Typography
name: 01-foundation-01-typography-01-typography
category: Foundation - Typography
---

The SDX Library includes the two standard fonts **TheSans** and **TheSerif**. It uses it's responsiveness to automatically adjust the font-size and properties depending on a series of media queries.

####Font Type Sans

The default style uses the **TheSans** font.

```html_example
<h4>Headline 4: Swisscom intends to continue reducing roaming prices</h4>
<p>Swisscom intends to continue reducing roaming prices</p>

```

####Font Type Serif

To change the font to **TheSerif** font-face add the class `serif` to any element that should be serif.

```html_example
<h4 class="font font--serif">Headline 4: Swisscom intends to continue reducing roaming prices</h4>
<p class="font font--serif">Swisscom intends to continue reducing roaming prices</p>
```

####Font Weight

To change the font weight form the default weight **Light** to a different weight add the classes `font--light`, `font--semi-light` or `font--semi-bold` to the element.

```html_example
<p class="font font--light">Swisscom intends to continue reducing roaming prices</p>
<p class="font font--semi-light">Swisscom intends to continue reducing roaming prices</p>
<p class="font font--semi-bold">Swisscom intends to continue reducing roaming prices</p>

```
*/

/*doc
---
title: Display Headings
name: 01-foundation-01-typography-02-display
category: Foundation - Typography
---

Traditional heading elements are designed to work best in the meat of your page content. When you need a heading to stand out, consider using a display heading, a larger slightly more opinionated heading style.<br/>
To add a display heading add one of the following classes `text-d1`, `text-d2` or `text-d3` to the element.

```html_example_table
<h1 class="text-d1">Display 1: Swisscom intends to continue reducing roaming prices</h1>
<h1 class="text-d2">Display 2: Swisscom intends to continue reducing roaming prices</h1>
<h1 class="text-d3">Display 3: Swisscom intends to continue reducing roaming prices</h1>
```
*/

