// --------------------------------------------------


// Headings
// -------------------------

h1,
h2,
h3,
h4,
h5,
h6,
p {
  @include font-sans;

  margin: 0;
  margin-bottom: $baseline-4;

  padding: 0;

  text-align: left;
  word-wrap: break-word;

  &:last-child {
    margin-bottom: 0;
  }
}

// Headline Styles
// -------------------------

h1,
.text-h1 {
  @include typo-headline-1;
  @include font-light;
}

h2,
.text-h2 {
  @include typo-headline-2;
  @include font-light;
}

h3,
.text-h3 {
  @include typo-headline-3;

  &,
  &--light {
    @include font-light;
  }

  &--semi-light {
    @include font-semi-light;
  }

  &--semi-bold {
    @include font-semi-bold;
  }
}

h4,
.text-h4 {
  @include typo-headline-4;

  &,
  &--light {
    @include font-light;
  }

  &--semi-light {
    @include font-semi-light;
  }

  &--semi-bold {
    @include font-semi-bold;
  }
}

h5,
.text-h5 {
  @include typo-headline-5;

  &,
  &--semi-light {
    @include font-semi-light;
  }

  &--semi-bold {
    @include font-semi-bold;
  }
}

h6,
.text-h6 {
  @include typo-headline-6;

  &,
  &--semi-light {
    @include font-semi-light;
  }

  &--semi-bold {
    @include font-semi-bold;
  }
}

// Display Styles
// -------------------------

.d1,
.text-d1 {
  @include typo-display-1;
  @include font-light;
}

.d2,
.text-d2 {
  @include typo-display-2;
  @include font-light;
}

.d3,
.text-d3 {
  @include typo-display-3;
  @include font-light;
}


// Body text
// -------------------------

p {
  @include typo-body-1;
  @include font-semi-light;
}

.text-b1 {
  @include typo-body-1;
  @include font-semi-light;
}

.text-b2 {
  @include typo-body-2;
  @include font-semi-light;
}

.text-sm {
  @include typo-small;
  @include font-semi-light;
}

// Misc Styles
// -------------------------

address {
  font-style: normal;
}

strong {
  @include font-semi-bold;
}

em {
  font-style: italic;
}


// Font Styles
// --------------------------------------------------

.font {
  @include font-sans;

  &--sans {
    @include font-sans;
  }

  &--serif {
    @include font-serif;
  }

  &--light {
    @include font-light;
  }

  &--semi-light {
    @include font-semi-light;
  }

  &--semi-bold {
    @include font-semi-bold;
  }
}

