/*doc
---
title: Pie charts
name: 02-components-08-pie-charts-01
category: Components - Pie charts
---

Pie charts are used to show percentage based data. Data is shown in segments of different colours,
with the sum total equalling 100%.

<script>
function getRandom(min, max) {
  return Math.floor(Math.random() * (max - min) + min);
}

function getRandomData() {
  var data = [
    {
      title: "Photos",
      color: "petrol"
    },
    {
      title: "Files",
      color: "orchid"
    },
    {
      title: "Videos",
      color: "apple"
    },
    {
      title: "Available",
      color: "#e0e6e9"
    }
  ]

  for (var i = 0; i < data.length; i++) {
    data[i].value = getRandom(2, 20);
  }

  return data;
}

document.addEventListener("DOMContentLoaded", function(event) {
  var pieChart = new sdx.PieChart(document.getElementById("pieChart01"));

  document.getElementById("btnChartUpdate").addEventListener("click", function () {
    var data = getRandomData();
    pieChart.update(data);
  });
});

</script>

<button class="button button--secondary" id="btnChartUpdate">Update</button>

```html_example
<div class="pie-chart" data-init="none" data-unit="GB" id="pieChart01">
  <div class="js-data-list">
    <div class="js-data" data-value="2" data-color="petrol">Photos</div>
    <div class="js-data" data-value="2" data-color="orchid">Files</div>
    <div class="js-data" data-value="3" data-color="apple">Videos</div>
    <div class="js-data" data-value="10" data-color="#e0e6e9">Available</div>
  </div>
  <div class="chart-wrapper">
    <div class="js-chart"></div>
    <div class="meta">
      <div class="title">15 GB</div>
      <div class="subtitle">Available</div>
    </div>
  </div>
  <ul class="pie-chart__legend js-legend"></ul>
</div>
```

You can either define the data elements in the markup and use the `data-init="auto"` attribute. Or update the data values using the provided JavaScript component.
The data list is expected to be in the following format:

```js
[
  {
    title: "Photos",
    color: "petrol",
    value: 12
  },
  {
    title: "Files",
    color: "orchid"
    value: 2
  }
]
```

**Title:** The segment data title<br>
**Color:** The sdx interaction color CSS class name or a valid hex color (eg `#ffffff`)<br>
**Value:** The value of the current segment as integer or floating point number<br>

In addition make sure to set the `data-unit` attribute to the desired value.

# Variations
*/

