title: Overview
name: 02-components-23-notification-modal-01-overview
category: Components - Notification Modal
---

Modal notifications are large screen take overs featuring messaging and iconography that intentionally interrupt the users experience, usually require an action from the user in order to progress to the next stage in an experience, or because the notification is of particular significance.

#### How to use notification modals
Notifications modals behave just like conventional modal windows, but with the introduction of bold colour. They're the only instance where the interaction colour palette is used as a background colour. They help to provide a clearly signposted and direct delivery of information to the user.

#### Interactive sample
A responsive test page for the Notification Modal dialog is available at [samples/notification-modal](/samples/notification-modal.html).
In additon to the responsive behavior it also demonstrates how to open and close the modal dialog.
