// --------------------------------------------------

$notification-margin: 48px;
$notification-inner-margin: 30px;

// --------------------------------------------------


// Notification Modal
// --------------------------------------------------

.modal--notification {
  .modal__content {
    background: $color-int-blue;

    width: 100%;
    height: 100%;

    @include bp-tablet {
      max-width: 460px;
      height: auto;
    }

    .modal__close {
      top: 22px;
      right: 22px;

      color: $color-white;
    }

    .modal__body {
      display: flex;
      flex-direction: column;
      align-items: center;

      height: 100%;

      padding: $notification-inner-margin;

      // Notification Message
      // -------------------------

      .notification-message {
        @include font-semi-light;
        @include typo-headline-4;
        @include font-smoothing;

        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-grow: 1;

        width: 100%;

        color: $color-white;
        text-align: center;

        .icon {
          font-size: 64px;

          margin-bottom: $notification-inner-margin - 10;
        }
      }

      // Notification Button
      // -------------------------

      .button {
        border: 1px solid $color-white;
        background-color: transparent;
        color: $color-white;

        margin-top: $notification-inner-margin;

        // Hover, Focus, Active styles
        // -------------------------

        &:hover,
        &:focus,
        &:active {
          border-color: rgba($color-white, .6);
        }
      }
    }
  }

  // Grid Container in Modal
  // --------------------------------------------------

  > .container {
    @include bp-xs-max {
      height: calc(100% - #{$notification-margin});
    }
  }

  // Notification Color
  // -------------------------

  &--alert {
    .modal__content {
      background: $color-int-orange;
    }
  }

  &--confirmation {
    .modal__content {
      background: $color-int-green;
    }
  }
}
