<script>
  function toggleToolbar(id) {
    var toolbar = new sdx.Toolbar(document.querySelector(id));
    toolbar.toggle();
  }
</script>

Provides a Toolbar with two alignments. Horizontal or vertical aligned.

Add the class `toolbar` to a wrapper div and place buttons inside the wrapper with the class `toolbar__item item--show` to add a toolbar.
