/*doc
---
title: Toolbar - Desktop
name: 02-components-18-toolbar-02-desktop
category: Components - Toolbar & Tooltips
---

The desktop toolbar uses Tooltips as text hint.

### Horizontal Toolbar

The horizontal Toolbar is the standard toolbar. You can add the class `toolbar--horizontal` next to the `toolbar`class but you don't have to.

<button class="button button--primary" onclick="toggleToolbar('#toolbar01')">Toggle Toolbar</button>

```html_example
<div class="toolbar" id="toolbar01">
  <button class="toolbar__item item--show tooltip" aria-label="Share">
    <i class="icon icon-197-share"></i>
  </button>
  <button class="toolbar__item item--show tooltip" aria-label="Edit">
    <i class="icon icon-025-edit"></i>
  </button>
  <button class="toolbar__item item--show tooltip" aria-label="Download">
    <i class="icon icon-024-download-cloud"></i>
  </button>
  <button class="toolbar__item item--show tooltip" aria-label="Delete">
    <i class="icon icon-008-bin"></i>
  </button>
</div>
```

#### Horizontal Toolbar on dark background

If the Toolbar component is embedded in a dark background it automatically adjusts its styling accoordingly.

```html_example
<div class="bg bg--dark">
  <div class="toolbar">
    <button class="toolbar__item item--show tooltip" aria-label="Share">
      <i class="icon icon-197-share"></i>
    </button>
    <button class="toolbar__item item--show tooltip" aria-label="Edit">
      <i class="icon icon-025-edit"></i>
    </button>
    <button class="toolbar__item item--show tooltip" aria-label="Download">
      <i class="icon icon-024-download-cloud"></i>
    </button>
    <button class="toolbar__item item--show tooltip" aria-label="Delete">
      <i class="icon icon-008-bin"></i>
    </button>
  </div>
</div>
```

