  <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Share">
    <i class="icon icon-197-share"></i>
  </button>
  <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Edit">
    <i class="icon icon-025-edit"></i>
  </button>
  <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Download">
    <i class="icon icon-024-download-cloud"></i>
  </button>
  <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Delete">
    <i class="icon icon-008-bin"></i>
  </button>
</div>
```

#### Vertical Toolbar on dark background

If the Toolbar component is embedded in a dark background it automatically adjusts its styling accoordingly.

```html_example
<div class="bg bg--dark">
  <div class="toolbar toolbar--vertical">
    <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Share">
      <i class="icon icon-197-share"></i>
    </button>
    <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Edit">
      <i class="icon icon-025-edit"></i>
    </button>
    <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Download">
      <i class="icon icon-024-download-cloud"></i>
    </button>
    <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Delete">
