  <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Share">
    <i class="icon icon-197-share"></i>
  </button>
  <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Edit">
    <i class="icon icon-025-edit"></i>
  </button>
  <button class="toolbar__item item--show tooltip tooltip--right" aria-label="Download">
