// --------------------------------------------------


// Toolbar wrapper styles
// --------------------------------------------------

.toolbar {
  @include font-sans;
  @include font-semi-light;

  &__item {
    display: inline-block;
    padding: 10px;

    text-align: center;
    color: $color-int-blue;

    opacity: 0;
    transform: translate(0, 10px);

    transition:
      color 180ms $standard-easing,
      opacity 300ms $standard-easing,
      transform 300ms cubic-bezier(.87, -.41, .19, 1.44);

    &:hover {
      color: $color-int-blue--active;

      .toolbar__label {
        color: $color-int-blue--active;
      }
    }

    .toolbar__label {
      display: block;

      font-size: 14px;
      color: $color-gray;
    }

    .icon {
      font-size: 30px;
    }

    &.item--show {
      opacity: 1;
      transform: translate(0, 0);
    }
  }


  &--vertical {
    .toolbar__item {
      display: block;

      opacity: 0;
      transform: translate(10px, 0);

      text-align: left;
      align-items: center;

      .toolbar__label {
        display: inline-block;
        margin-left: 12px;

        line-height: 1;
      }

      .icon {
        vertical-align: middle;
      }

      &.item--show {
        opacity: 1;
        transform: translate(0, 0);
      }
    }
  }

  // Disabled style
  // -------------------------

  .item--disabled,
  %item--disabled {
    pointer-events: none;

    color: $color-disabled;

    .toolbar__label {
      color: $color-disabled;
    }
  }

  &.toolbar--disabled {
    .toolbar__item {
      @extend %item--disabled;
    }
  }
}

// Dark Background
// -------------------------
.bg--dark {
  .toolbar__item {
    &:hover {
      .toolbar__label {
        color: $color-white;
        text-decoration: underline;
      }
    }

    .toolbar__label {
      color: $color-white;
    }
  }

  // Disabled style
  // -------------------------

  .toolbar {
    .item--disabled,
    %item--disabled--dark {
      pointer-events: none;

      opacity: .4;
      color: $color-white;

      .toolbar__label {
        color: $color-white;
      }
    }

    &.toolbar--disabled {
      .toolbar__item {
        @extend %item--disabled--dark;
      }
    }
  }
}
