## Regular flyout menu

Regular menus, or flyouts are used to reveal functionality thats behind an icon other than the navigation, such as settings and preferences, or glance views of your basket.

```html_example
<div class="text-align-right">
  <button data-init="auto", data-toggle="flyout", data-target="#flyout-list">
    <i class="icon icon-001-account icon--s2"></i>
  </button>
</div>
<div id="flyout-list" class="flyout flyout--list flyout--right js-flyout" style="top: 70px; right: 6px;">
  <ul>
    <li class="text-align-center">
      <a class="list-link flyout__indicator" href="javascript:void(0);">Deutsch</a>
    </li>
    <li class="text-align-center">
      <a class="list-link" href="javascript:void(0);">English</a>
    </li>
    <li class="text-align-center">
      <a class="list-link" href="javascript:void(0);">Franais</a>
    </li>
    <li class="text-align-center">
      <a class="list-link" href="javascript:void(0);">Italiano</a>
    </li>
  </ul>
</div>
```

## Flyout with CTAs

These menus can also be used to house text, and often feature titles to give a welcome message or title to explain the purpose of the menu. They can also contain a call to action (CTA), following the conventional logic for buttons.

```html_example
<div class="text-align-right">
  <button data-init="auto", data-toggle="flyout", data-target="#flyout-info">
    <i class="icon icon-001-account icon--s2"></i>
  </button>
</div>
<div id="flyout-info" class="flyout flyout__indicator flyout--info flyout--right js-flyout" style="top: 70px; right: 6px;">
  <div class="flyout__title">Welcome to my Swisscom</div>
  <div class="divider"></div>
  <div class="flyout__text">
    Log in to manage your invoices, products, orders and see more offers
  </div>
  <div class="button-group button-group--fill">
    <button class="button button--secondary">Cancel</button>
    <button class="button button--primary">Login</button>
  </div>
</div>
```
