// --------------------------------------------------


.flyout {
  display: none;

  position: absolute;
  top: 20px;

  opacity: 0;
  transform: translateY(-20px);

  z-index: $zindex-menu;

  background: $color-white;
  box-shadow: 0 0 4px 0 rgba($color-black, .2);


  &.is-open {
    display: block;
    opacity: 1;

    transform: translateY(0);
  }

  // Menu Position
  // -------------------------

  &--left {
    left: 0;
  }

  &--right {
    right: 0;
  }

  // -------------------------

  &__indicator::before {
    position: absolute;
    top: -7px;
    right: 24px;

    width: 14px;
    height: 14px;

    background: inherit;

    box-shadow: -1px -1px 2px 0 rgba($color-black, .15);

    transform: rotate(45deg);

    content: '';
  }

  &__content {
    background: inherit;
  }

  // -------------------------

  &__title {
    margin-top: -4px;

    text-align: center;
  }

  &__text {
    margin-bottom: 16px;

    text-align: center;
  }

  // -------------------------

  &--list {
    width: 254px;

    padding: 0;

    li:not(:last-child) {
      border-bottom: 1px solid $color-aluminum-80;
    }

    .list-link {
      display: block;

      width: 100%;
      padding: 12px 24px;

      border: 0;

      background: $color-white;

      &:hover {
        color: $color-white;
        background: $color-int-blue;
      }
    }
  }

  &--info {
    width: 369px;

    padding: 24px;

    hr,
    .divider {
      margin: 16px 0;
    }
  }
}
