*/

&__input {

  // Search input styles
  // -------------------------

  display: block;
  overflow: hidden;

  position: relative;

  width: 100%;

  background: $color-search-inactive-bg;
  color: $color-search-inactive-text;

  transition:
    background 200ms $standard-easing,
    color 200ms $standard-easing;

  .search__input-wrapper {
    display: flex;
    position: relative;

    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: $search-height;
  }

  .search__box {
    display: flex;
    width: 100%;
  }

  .search__field {
    @include typo-search;

    // fixes edge line height bug
    @supports(-ms-ime-align: auto) {
      line-height: 0;
    }

    display: block;

    width: 100%;

    vertical-align: middle;

    transform: translateX(0);
    transition: transform 200ms $standard-easing;

    &::placeholder {
      color: $color-search-inactive-text;
      opacity: 1;
    }
  }

  .search__field:invalid {
    transform: translateX(-#{$search-clear-size + $search-box-margin});

    ~ .search__icon-clear {
      visibility: hidden;
      opacity: 0;
      transform: scale(0);
    }
  }

  .search__icon {
    &-clear {
      margin-right: $search-box-margin;

      z-index: 10;
      order: -1;

      font-size: $search-clear-size;

      visibility: visible;
      opacity: 1;
      transform: scale(1);

      transition:
        visibility 200ms $standard-easing,
        opacity 200ms $standard-easing,
        transform 200ms $standard-easing;
    }

    &-close,
    &-search {
      margin-left: $search-box-margin;

      font-size: $search-icon-size;
    }
  }

  // Color options
  // --------------------------------------------------

  @mixin search-light {
    background: $color-search-light-bg;
    color: $color-search-light-text;

    .search__field {
      &,
      &::placeholder {
        color: $color-search-light-text;
        opacity: 1;
      }

      &:focus {
        &::placeholder {
          color: $color-search-light-text;
          opacity: 1;
        }
      }
    }
  }

  @mixin search-dark {
    background: $color-search-dark-bg;
    color: $color-search-dark-text;

    .search__field {
      &,
      &::placeholder {
        color: $color-search-dark-text;
        opacity: 1;
      }

      &:focus {
        &::placeholder {
          color: $color-search-dark-text;
          opacity: 1;
        }
      }
    }
  }

  &,
  &.search--light {
    &.is-active {
      @include search-light;
    }

    @include bp-tablet {
      @include search-light;
    }
  }

  &.search--dark {
    &.is-active {
      @include search-dark;
    }

    @include bp-tablet {
      @include search-dark;
    }
  }
}

// Desktop Option
// --------------------------------------------------

&--desktop {
  .search__input {

    @include bp-tablet {
      max-height: 0;

      transition:
        max-height 200ms $standard-easing,
        background 200ms $standard-easing,
        color 200ms $standard-easing;

      &.is-open {
        max-height: $search-height;
      }
    }
  }
}
