Empty states are intend to prompt a user to interact in various different ways with our products.
The icon, messaging and CTAs are a constant feature that are interchangeable depending on its intended purpose.

## How to use empty states
Use empty states on user generated content products to prompt the interaction of users.
They should use text, iconography and button CTAs to encourage user participation.
There's also used to help clarify that pages and products have loaded successfully.

The empty state uses the form `<input type="file">` element for handling files. You can specify the
allowed file types for the file picker on mobile by setting the `mime-type` in the `accept` attribute.
For more information see [HTML DOM Input FileUpload Object](http://www.w3schools.com/jsref/dom_obj_fileupload.asp).

Make sure to validate the file-type before uploading the files.

For the `EmptyState` component to be dispalyed properly
make sure to either set the attribute `data-init="auto"` on the component, or the initialize it using the JavaScript API.

<style>
