                <label class="button button--primary" for="emptystate">Upload</label>
            </div>
        </div>
    </form>
</div>
```

## Variations

### Full screen empty states
Empty states can take over the whole screen in a product to prompt the user to complete an action.
In this example, the empty state is explaining how to upload a file.

[samples/empty-state](/samples/empty-state.html)

### Empty states in modals
Modals can also be used to provide an empty state prompt to the user.
A modal empty state is useful where space is more limited within a product.

[samples/empty-state-modal](/samples/empty-state-modal.html)
