// Generic-Table wrapper styles
// -------------------------

.table-generic {
  display: block;

  .table__title {
    margin-bottom: 16px;
    border-bottom: 1px solid $color-table-border;
  }

  .table__wrapper {
    > .row {
      margin-left: 0;
      margin-right: 0;

      padding-left: 16px;
      padding-right: 16px;

      > [class*='col-'] {
        padding: 0;
        flex: 1;
      }
    }

    .table-items {
      display: flex;
      justify-content: space-around;

      height: 100%;
    }

    .table-item {
      display: inline-block;

      margin: 0 12px;
    }

    .table-image {
      width: 80px;
      height: auto;
    }
  }

  // -------------------------

  &--shadow {
    padding: 19px 0 16px;

    box-shadow: 0 0 4px 0 rgba($color-black, .25);
  }

  // generic table styles
  // -------------------------

  .highlight {
    background: $color-table-header-dark;
  }

  .border {
    margin: 16px;
    padding: 16px;
    border: 1px solid $color-table-border;

    &-horizontal {
      margin: 16px 0;
      border-bottom: 1px solid $color-table-border;
    }

    &-vertical {
      margin: 0 16px;
      border-right: 1px solid $color-table-border;
    }
  }
}
