// --------------------------------------------------


// Reset List
// -------------------------

ul,
menu,
dir {
  display: block;
  padding: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

// List Base
// --------------------------------------------------

ul.list ,
ol.list {
  margin-top: 29px;
  margin-bottom: 28px;

  ul.list ,
  ol.list {
    margin-top: 0;
    margin-bottom: 0;

    padding-left: 18px;
  }

  li {
    margin-bottom: 13px;

    @include bp-sm {
      margin-bottom: 12px;
    }

    &::before {
      display: inline;
      float: left;

      color: $color-blue;

      content: '';
    }

    &.single-line {
      @extend %single-line;
    }
  }

  &.single-line > li {
    @extend %single-line;
  }
}


// Single Line Extension
// -------------------------

%single-line {
  margin-bottom: 7px;

  @include bp-sm {
    margin-bottom: 6px;
  }
}


// Description Lists
// -------------------------

dt.list {
  font-weight: 300;
}


// Bullet List
// --------------------------------------------------

ul.list {
  li {
    margin-left: 18px;

    &::before {
      margin-top: 1px;
      margin-left: -20px;
      font-size: 33px;

      content: '';
    }
  }
}


// Numbered List
// --------------------------------------------------

ol.list {
  li {
    margin-left: 32px;
    counter-increment: item;

    @include bp-sm {
      margin-left: 30px;
    }

    &::before {
      margin-left: -32px;
      font-weight: 600;

      content: counter(item) '.';

      @include bp-sm {
        margin-left: -30px;
      }
    }
  }
}
