Use of the checkbox element.

```html_example
<form>
  <div class="checkbox">
    <input type='checkbox' name='dummy' id='checkbox1-1'>
    <label for='checkbox1-1'>Option 1</label>
  </div>
  <div class="checkbox">
    <input type='checkbox' name='dummy' id='checkbox1-2'>
    <label for='checkbox1-2'>Option 2<br/>Second Line</label>
  </div>
  <div class="checkbox">
    <input type='checkbox' name='dummy' id='checkbox1-3'>
    <label for='checkbox1-3'>Option 1</label>
    <p>Supporting description text</p>
  </div>
  <div class="checkbox">
    <input type='checkbox' name='dummy' id='checkbox1-4'>
    <label for='checkbox1-4'>Option 2<br/>Second Line</label>
    <p>Supporting description text</p>
  </div>
  <div class="checkbox checkbox--only">
    <input type='checkbox' name='dummy' id='checkbox1-5'>
    <label for='checkbox1-5'></label>
  </div>
</form>
```

### Inverted States

Adding the class `checkbox--inverted` to the `checkbox` element will invert the checkbox to have a blue background when selected.

```html_example
<form>
  <div class="checkbox checkbox--inverted">
    <input type='checkbox' name='dummy' id='checkbox2-1'>
    <label for='checkbox2-1'>Option 1</label>
  </div>
  <div class="checkbox checkbox--inverted">
    <input type='checkbox' name='dummy' id='checkbox2-2'>
    <label for='checkbox2-2'>Option 2<br/>Second Line</label>
  </div>
  <div class="checkbox checkbox--inverted">
    <input type='checkbox' name='dummy' id='checkbox2-3'>
    <label for='checkbox2-3'>Option 1</label>
    <p>Supporting description text</p>
  </div>
  <div class="checkbox checkbox--inverted">
    <input type='checkbox' name='dummy' id='checkbox2-4'>
    <label for='checkbox2-4'>Option 2<br/>Second Line</label>
    <p>Supporting description text</p>
  </div>
  <div class="checkbox checkbox--inverted checkbox--only">
    <input type='checkbox' name='dummy' id='checkbox2-5'>
    <label for='checkbox2-5'></label>
  </div>
</form>
```

### Disabled States

```html_example
