  </div>
  <div class="checkbox">
    <input type='checkbox' name='dummy' id='checkbox3-2' checked disabled>
    <label for='checkbox3-2'>Option 2<br/>Second Line</label>
    <p>Supporting description text</p>
  </div>
  <div class="checkbox checkbox--inverted">
    <input type='checkbox' name='dummy' id='checkbox3-3' disabled>
    <label for='checkbox3-2'>Option 1</label>
    <p>Supporting description text</p>
  </div>
  <div class="checkbox checkbox--inverted">
    <input type='checkbox' name='dummy' id='checkbox3-4' checked disabled>
    <label for='checkbox3-4'>Option 2<br/>Second Line</label>
    <p>Supporting description text</p>
  </div>
</form>
```

### Inline checkboxes

Add the class `form-inline` to a wrapper `div` element to set the checkboxes to inline.

```html_example
<form>
  <div class="form-inline">
    <div class="checkbox">
      <input type='checkbox' name='dummy' id='checkbox4-1'>
      <label for='checkbox4-1'>Option 1</label>
    </div>
    <div class="checkbox">
      <input type='checkbox' name='dummy' id='checkbox4-2'>
      <label for='checkbox4-2'>Option 2</label>
    </div>
  </div>
</form>
```

